body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.registerContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-image: linear-gradient(120deg, #f093fb 0%, #f5576c 100%); */
    background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
}

.registerCard {
    width: 90%;
    height: 90%;
    background-color: white;
    display: grid;
    grid-template-columns: 50% 50%;
}

.registerCard > form, .registerCard > div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
}

.signInBtn {
    margin-top: 20px;
}

.passwordInput {
    margin-top: 10px;
}

.accountQuestion {
    opacity: .5;
}

.accountQuestion a {
    font-weight: bold;
    cursor: pointer;
}

.registerCard span {
    color: red;
    font-size: 12px;
}

.svgContainer {
    -webkit-transform: scale(.7);
            transform: scale(.7)
}

@media only screen and (max-width: 600px) {
    .registerCard {
      grid-template-rows: 65% 35%;
      grid-auto-flow: column;
      grid-template-columns: 100%;
    }

}


.myBtn {
    position: relative;
    padding: 10px 20px;
    font-weight: bold;
    border: 3px transparent solid;
    border-image: linear-gradient(120deg, #f093fb 0%, #f5576c 100%);
    border-image-slice: 1;
    cursor: pointer;
}

.myBtn div {
    position: relative;
    z-index: 10;
    color: black;
    transition: color 250ms;
}

.myBtn:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background-image: linear-gradient(120deg, #f093fb 0%, #f5576c 100%);
    z-index: 2;
    transition: width 250ms;
}

.myBtn:hover:before {
    width: 100%;
}

.myBtn:hover div {
    color: white;
}

.myInput {
    width: 250px;
    position: relative;
}

.myInput input {
    width: calc(100% - 20px);
    height: 25px;
    padding: 10px;
    border: none;
    outline: none;
    font-weight: bold;
}

.myInput svg, .myInput svg rect {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    fill: transparent;
    pointer-events: none;
}

.myInput rect {
    stroke: black;
    stroke-width: 4;
    stroke-dasharray: 850;
    stroke-dashoffset: 600;
    transition: stroke-dasharray 500ms, stroke-dashoffset 500ms, stroke 500ms;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}

.input:focus + svg rect{
    stroke-dasharray: 700;
    stroke-dashoffset: 100;
    stroke: #26db81;
}

.inputError {
    stroke-dasharray: 700 !important;
    stroke-dashoffset: 100 !important;
    stroke: #db2626 !important;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(73, 73, 73);
    font-weight: bold;
    font-size: 20px;
    cursor: pointer;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
}

.shadow {
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.226);
}

.avatar:hover {
    -webkit-transform: scale(.9);
            transform: scale(.9);
}



.dashboardContainer {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}


.chatList {
    background-color: #FFFFFF;
}

.chat {
    background-color: #F5F6F8;
}
.appBar {
    background-color: #63BB67;
    display: flex;
    flex-direction: column;
    align-items: center;
    float: left;
    height: 100vh;
}

.avatar {
    margin-top: 30px;
    width: 10vw;
    height: 10vw;
    max-width: 50px;
    max-height: 50px;
}

.iconContainer {
    width: 100%;
    margin-top: 100px;
}

.checked {
    background-color: rgba(255, 255, 255, 0.205);
}

.appSvgContainer {
    width: 15vw;
    height: 15vw;
    max-width: 80px;
    max-height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background 250ms;
    overflow: hidden;
}

.appSvgContainer:hover {
    background-color: rgba(255, 255, 255, 0.205);
}

.appSvgContainer #chat {
    width: 40%;
}

.appSvgContainer:hover #chat {
    -webkit-animation: chatAnim 1s;
            animation: chatAnim 1s;
}

@-webkit-keyframes chatAnim {
    0% {-webkit-transform: translate(0);transform: translate(0)}
    50% {-webkit-transform: translate(50px, -50px);transform: translate(50px, -50px)}
    51% {-webkit-transform: translate(-50px, 50px);transform: translate(-50px, 50px)}
    100% {-webkit-transform: translate(0);transform: translate(0)}
}

@keyframes chatAnim {
    0% {-webkit-transform: translate(0);transform: translate(0)}
    50% {-webkit-transform: translate(50px, -50px);transform: translate(50px, -50px)}
    51% {-webkit-transform: translate(-50px, 50px);transform: translate(-50px, 50px)}
    100% {-webkit-transform: translate(0);transform: translate(0)}
}

.appSvgContainer #contacts {
    -webkit-transform: scale(1.5);
            transform: scale(1.5)
}

.appSvgContainer:hover #contacts circle {
    -webkit-animation: contactsAnim 1s ease-in-out;
            animation: contactsAnim 1s ease-in-out;
}

.appSvgContainer #contacts circle:nth-child(2) {
    -webkit-animation-delay: 250ms;
            animation-delay: 250ms;
}

@-webkit-keyframes contactsAnim {
    0% {
        r: 2.6;
        cy: 2.6;
    }
    50% {
        r: 2;
        cy: 0;
    }
    100% {
        r: 2.6;
        cy: 2.6;
    }
}

@keyframes contactsAnim {
    0% {
        r: 2.6;
        cy: 2.6;
    }
    50% {
        r: 2;
        cy: 0;
    }
    100% {
        r: 2.6;
        cy: 2.6;
    }
}

.appSvgContainer #settings {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    transition-duration: 1s;
}

.appSvgContainer:hover #settings {
    -webkit-animation: settingsAnim 1s;
            animation: settingsAnim 1s;
}

@-webkit-keyframes settingsAnim {
    0% {
        -webkit-transform: rotate(0deg) scale(1.3);
                transform: rotate(0deg) scale(1.3)
    }
    50% {
        -webkit-transform: rotate(360deg) scale(1);
                transform: rotate(360deg) scale(1);
    }
    100% {
        -webkit-transform: rotate(0deg) scale(1.3);
                transform: rotate(0deg) scale(1.3)
    }
}

@keyframes settingsAnim {
    0% {
        -webkit-transform: rotate(0deg) scale(1.3);
                transform: rotate(0deg) scale(1.3)
    }
    50% {
        -webkit-transform: rotate(360deg) scale(1);
                transform: rotate(360deg) scale(1);
    }
    100% {
        -webkit-transform: rotate(0deg) scale(1.3);
                transform: rotate(0deg) scale(1.3)
    }
}

.chatList {
    width: 100%;
    position: absolute;
    left: 80px;
    height: 100vh;
    max-height: 100vh;
    max-width: 400px;
    overflow-y: auto;
    overflow: visible;
    box-shadow: 5px 0 20px 10px rgba(156, 181, 197, 0.1);
    float: left;
    z-index: 20;
    transition: width 500ms ease-in-out 0ms;
}

.listContainer {
    overflow: hidden;
    width: 100%;
    transition: opacity 250ms;
    opacity: 1;
}

.listConClose {
    opacity: 0;
}

.listClose {
    width: 0;
}

.chatListHeader {
    width: calc(100% - 40px);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    color: #404751;
}

.chatListHeader > p {
    color: #4EB3F3;
    text-decoration: underline;
    cursor: pointer;
}

.searchField {
    margin: 0px 20px 20px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.chatList input {
    outline: none;
    width: 100%;
    height: 50px;
    border: none;
    margin-left: 15px;
    font-size: 15px;
    font-weight: bold;
    color: #404751;
}

.chatListItem {
    padding: 30px 20px;
    cursor: pointer;
}

.chatSelected {
    background-color: rgba(126, 126, 126, 0.055);
}

.chatListAvatar {
    margin: 0;
    margin-right: 20px;
}

.chatListItem h2 {
    color: #404751;
    font-size: 20px;
}

.chatListItem p {
    color: #404751ab;
    font-size: 15px;
}

.chatListItem > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.avaName {
    display: flex;
    align-items: center;
}

.openCloseChatList {
    width: 40px;
    height: 80px;
    background-color: white;
    position: absolute;
    top: calc(50vh - 60px);
    right: -40px;
    border-radius: 0 5px 5px 0;
    box-shadow: 10px 0 10px rgba(156, 181, 197, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.openCloseChatList svg {
    width: 30px;
    transition: -webkit-transform 500ms;
    transition: transform 500ms;
    transition: transform 500ms, -webkit-transform 500ms;
}

.svgOpen {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg)
}

.svgClose {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
}

@media only screen and (max-width: 600px) {
    .chatList {
        width: calc(100% - 40px);
        position: absolute;
        max-width: calc(100% - 40px);
        overflow-y: auto;
        overflow: visible;
        float: none;
        left: 0;
    }
    .listConClose {
        opacity: 0;
    }
    
    .listClose {
        width: 0;
    }
    .openCloseChatList {
        top: calc(100vh - 90px);
    }
} 
.chat {
    height: 100vh;
    display: grid;
    grid-template-rows: 100px auto 70px;
    max-height: 100vh;
}

.chatHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    height: 100%;
}

.chatHeader > div {
    display: flex;
    align-items: center;
}

.chatHeader > div > h2 {
    color: #404751;
    font-size: 15px;
}

.chatAvatar {
    margin: 0;
    margin-right: 20px;
    width: 40px;
    height: 40px;
    font-size: 13px;
}

.moreBtnContainer {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    transition: background .5s;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.moreBtnContainer > svg {
    width: 25px;
    height: 25px;
}

.moreBtnContainer:hover {
    background-color: rgba(0, 0, 0, 0.1);
}

.messages {
    max-height: calc(100vh - 170px);
    overflow-y: auto;
}

.messagesAvatar {
    width: 40px;
    height: 40px;
    font-size: 13px;
}

.messageContainerLeft {
    display: flex;
    align-items: flex-end;
    margin: 20px;
}

.messageContainerRight {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
    margin: 20px;
}

.message {
    max-width: calc(80% - 40px);
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 20px;
    margin: 0 10px;
    border-radius: 10px 10px 10px 0;
}

.blue {
    background-color: #43AEF3;
    border-radius: 10px 10px 0 10px;
}

.message>h3 {
    color: #404751;
    margin: 0;
}

.message>p{
    color: #40475196;
    margin: 0;
    margin-top: 10px;
}

.blue>h3{
    color: #ffffff;
}

.blue>p{
    color: #e0f5ff;
}

.messageInput {
    background-color: white;
    width: 100%;
    display: grid;
    grid-template-columns: 70px auto 70px 70px;
}

.messageInput input {
    outline: none;
    border: none;
    font-size: 15px;
    padding: 0 10px;
}

.chatSvgContainer {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background 500ms;
    cursor: pointer;
}

.chatSvgContainer:hover {
    background-color: rgba(104, 104, 104, 0.1);
}

@media only screen and (max-width: 600px) {
    .chatHeader {
        display: flex;
        justify-content: space-between;
        padding: 0 10px;
    }
    .chat {
        grid-template-rows: 100px auto 40px;
    }
    .messages {
        max-height: calc(100vh - 140px);
        overflow-y: auto;
    }
    .chatSvgContainer {
        height: 40px;
        width: 40px;
    }
    .messageInput {
        grid-template-columns: 40px auto 40px 40px;
    }

    .messageInput input {
        font-size: 12px;
    }

    .messagesAvatar {
        width: 20px;
        height: 20px;
        font-size: 10px;
    }
    
    .messageContainerLeft {
        margin: 10px;
    }
    
    .messageContainerRight {
        margin: 10px;
    }
    
    .message {
        max-width: calc(80% - 30px);
        padding: 15px;
        margin: 0 10px;
    }
}

.newChatContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.1);
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
}

.newChat {
    position: relative;
    width: 80%;
    height: 80%;
    max-width: 500px;
    background-color: white;
    box-shadow: 0 5px 20px 10px rgba(180, 180, 180, 0.2);
    border-radius: 10px;
    overflow-y: hidden;
    z-index: 101;
}

.newChatClickBack {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: white;
    z-index: -20;
}

.groupBack {
    z-index: -10;
    background-color: transparent;
}

.newChatHeader>h1 {
    margin: 0;
    padding: 20px 0;
    text-align: center;
    color: #404751;
}

.newChatHeader>input {
    width: 80%;
    background-color: transparent;
    border: 0;
    height: 50px;
    outline: none;
    font-size: 17px;
}

.newChatHeader {
    background-color: #F5F6F8;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.newChatUserContainer {
    max-height: 82%;
    overflow-y: auto;
}

.newChatUser {
    margin: 10px;
    padding: 2px 10px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 5px 7px 2px rgba(193, 202, 204, 0.233);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.newChatUser input {
    margin-right: 10px;
}

.newChatSpace {
    height: 50px;
}

.newChatFab {
    position: absolute;
    height: 60px;
    width: 60px;
    border-radius: 30px;
    background-color: #63BB67;
    bottom: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 5px 7px 2px rgba(193, 202, 204, 0.3);
}

.groupPopUp {
    position: absolute;
    top: 35%;
    left: 5%;
    width: 90%;
    height: 30%;
    background-color: white;
    box-shadow: 0 5px 20px 10px rgba(180, 180, 180, 0.2);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.groupPopUp>button {
    margin-top: 20px;
    background-color: #43AEF3;
    padding: 10px 20px;
    outline: none;
    color: white;
    border: none;
    border-radius: 5px;
}

@media only screen and (max-width: 600px) {
    .newChat {
        width: 90%;
        height: 90%;
    }
}
